.modal {
  $self: &;
  width: 90%;
  max-width: 396px;
  padding: 30px 50px;
  background-color: $color-white;

  @include respond-to("phone-mid") {
    padding: 30px;
  }



  &__title {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 1;
    font-weight: 700;
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 12px;
    width: 14px;
    height: 14px;
    padding: 0;
    border: none;
    background-color: transparent;
    appearance: none;
    cursor: pointer;

    svg {
      display: block;
      fill: $color-blue;
      transition: 0.2s;
    }

    @include respond-to-min("desktop") {
      &:hover,
      &:focus {
        outline: none;

        svg {
          color: $color-blue-2;
        }
      }

      &:active {
        opacity: 0.7;
      }
    }
  }

  &__content {
    font-family: $secondFont;
    font-size: 15px;
    line-height: calc(26 / 15);
    color: $color-dark-5a;
  }

  &--tags {
    max-width: 632px;
    padding: 48px 40px 34px 36px;

    #{$self}__content {
      max-height: 320px;
      overflow: auto;
    }
  }

  .application-form-submit {
    flex-direction: column;

    .calc-btn {
      width: 100%;
      max-width: none;
      margin: 0 0 20px;
    }
  }
}
