.radio-input {
  position: relative;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 48px;
  padding: 0 16px;
  background: #f4f4f4; }
  .radio-input input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    display: block;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer; }
    .radio-input input + b {
      position: relative;
      z-index: 2;
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 20px;
      height: 20px;
      background: #ffffff;
      border: 1px solid #e1e5ed;
      border-radius: 50%; }
    .radio-input input:checked + b:before {
      content: "";
      position: absolute;
      display: block;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background: #426cff; }
    .radio-input input:checked ~ span {
      color: #f4f4f4; }
    .radio-input input:checked ~ i {
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: #688aff; }
  .radio-input span {
    position: relative;
    z-index: 2;
