.social {
  display: flex;
  align-items: center;

  &__item {
    margin-right: 12px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: block;
    height: 20px;

    svg {
      display: block;
      height: 100%;
      fill: $color-blue;
      transition: 0.2s;
    }

    @include respond-to-min("desktop") {
      &:hover,
      &:focus {
        outline: none;

        svg {
          fill: $color-blue-2;
        }
      }

      &:active {
        opacity: 0.7;
      }
    }
  }
}
