.contact-button {
  $self: &;
  position: fixed;
  top: 50%;
  right: 0;
  z-index: 9;
  transform: translateY(-50%);
  padding: 26px 15px;
  border-radius: 5px 0 0 5px;
  border: solid 1px $color-blue;
  border-right: none;
  background-color: $color-white;
  appearance: none;
  cursor: pointer;
  transition: 0.2s;

  @include respond-to("phablet") {
    padding: 16px 8px;
  }

  &__icon {
    display: block;
    margin-bottom: 8px;

    svg {
      display: block;
      fill: $color-blue;
      transition: 0.2s;

      @include respond-to("phablet") {
        width: 16px;
        height: 16px;
      }
    }
  }

  &__text {
    font-size: 19px;
    line-height: 1;
    color: $color-blue;
    writing-mode: vertical-lr;
    transition: 0.2s;

    @include respond-to("phablet") {
      font-size: 15px;
    }
  }
  @include respond-to-min("desktop") {
    &:hover,
    &:focus {
      outline: none;
      background-color: $color-blue;

      #{$self}__icon {
        svg {
          fill: $color-white;
        }
      }

      #{$self}__text {
        color: $color-white;
      }
    }

    &:active {
      opacity: 0.7;
    }
  }
}
